<template>
  <div class="home">
    <div class="try">
      <div id="titleProxPartido">
        <h3 class="black--text">{{text}}</h3>
      </div>
      <div id="FechaHoraPartido">
        <h2>{{date}}</h2>
        <h2>{{hour}}</h2>
      </div>
      <div id="MatchLayout">
        <div id="localTeam" class="teamcontainer">
          <v-img :src="localTeam.img" width="80px" class="hidden-md-and-up"></v-img>
          <v-img :src="localTeam.img" width="150px" class="hidden-sm-and-down"></v-img>
          <p>{{localTeam.name}}</p>
        </div>

        <div v-if="showVS" class="resultcontainer">
          <v-img
            src="../assets/Extras/Marcadores/nvs.png"
            width="50px"
            style="margin: 5px"
            class="hidden-md-and-up"
          ></v-img>

          <v-img
            src="../assets/Extras/Marcadores/nvs.png"
            width="75px"
            style="margin: 5px"
            class="hidden-sm-and-down"
          ></v-img>
        </div>

        <div v-if="showResult" id="resultScore" class="resultcontainer">
          <v-img :src="localTeam.score" width="50px" style="margin: 5px" class="hidden-md-and-up"></v-img>

          <v-img :src="localTeam.score" width="75px" style="margin: 5px" class="hidden-sm-and-down"></v-img>

          <p style="font-size:75px" class="spacer">-</p>

          <v-img
            :src="visitanteTeam.score"
            width="50px"
            style="margin: 5px"
            class="hidden-md-and-up"
          ></v-img>

          <v-img
            :src="visitanteTeam.score"
            width="75px"
            style="margin: 5px"
            class="hidden-sm-and-down"
          ></v-img>
        </div>

        <div id="visitanteTeam" class="teamcontainer">
          <v-img :src="visitanteTeam.img" width="80px" class="hidden-md-and-up"></v-img>
          <v-img :src="visitanteTeam.img" width="150px" class="hidden-sm-and-down"></v-img>
          <p>{{visitanteTeam.name}}</p>
        </div>
      </div>

      <v-alert type="info" id="homeComment">{{comment}}</v-alert>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import db from "@/fb";
import info from "@/information";

export default {
  data: function() {
    return {
      noMatch: false,
      text: null,
      comment: null,
      hour: null,
      showVS: false,
      showResult: true,
      date: null,
      localTeam: {
        name: null,
        img: null,
        score: null,
      },
      visitanteTeam: {
        name: null,
        img: null,
        score: null
      }
    };
  },
  created: function() {
    db.collection("Home").onSnapshot(res => {
      const changes = res.docChanges();

      changes.forEach(change => {
        if (change.type === "added") {
          let t = change.doc.data();
          this.comment = t.Comentario;
          this.hour = t.HoraPartido;
        }
      });
    });

    this.init();

  },
  methods: {
    init: function() {
      Date.prototype.getWeek = function() {
        var onejan = new Date(this.getFullYear(), 0, 1);
        return Math.ceil(
          ((this - onejan) / 86400000 + onejan.getDay() + 1) / 7
        );
      };

      let today = new Date();

      let todayWeek = today.getWeek();

      let matchIndex = this.lookForMatch(todayWeek);

      if (matchIndex == -1) {
        this.noMatch = true;
      } else {
        this.noMatch = false;

        this.date = info.matches[matchIndex].day;
        this.localTeam.name = info.matches[matchIndex].localTeam.name;
        this.localTeam.img = require("@/assets/Teams/" +
          info.matches[matchIndex].localTeam.logo);
        this.visitanteTeam.name = info.matches[matchIndex].visitanteTeam.name;
        this.visitanteTeam.img = require("@/assets/Teams/" +
          info.matches[matchIndex].visitanteTeam.logo);

        if (info.matches[matchIndex].result == "-") {
          this.showVS = true;
          this.showResult = false;
          this.text = "Prximo partido:";
        } else {
          this.showVS = false;
          this.showResult = true;
          this.text = "Resultado del partido:";

          let score = this.getScore(info.matches[matchIndex].result);

          this.localTeam.score = require("@/assets/Extras/Marcadores/n" +
            score[0] +
            ".png");
          this.visitanteTeam.score = require("@/assets/Extras/Marcadores/n" +
            score[1] +
            ".png");
        }
      }
    },
    getScore: function(j) {
      return j.split("-");
    },
    splitDate: function(fecha) {
      let array = fecha.split("/");

      array[0] = Number(array[0]);
      array[1] = Number(array[1]);
      array[2] = Number(array[2]);

      return array.reverse();
    },
    lookForMatch: function(week) {
      let found = false;
      let i = 0;
      let size = info.matches.length;

      while (i < size && !found) {
        let array = this.splitDate(info.matches[i].date);

        let matchWeek =new Date(array[0],array[1]-1, array[2]).getWeek() - 1;

        if (matchWeek == week) {
          found = true;
        } else {
          i++;
        }
      }

      if (found) {
        return i;
      } else {
        return -1;
      }
    }
  }
};
</script>
